import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import { Pressable, Modal, Text, View } from 'react-native';
import { RadioButton, Surface, Switch } from 'react-native-paper';
import GameContext from '../../Context/GameContext/GameContext';
import BaseCss from '../../Styles/BaseCss';

// Allows players to confirm quit.
const Settings = ({ modalVisible, setModalVisible }) => {
  const { dice, settings, dispatch } = useContext(GameContext);

  const toggleConfirm = () => {
    dispatch({
      type: 'settings',
      payload: {
        ...settings,
        confirmMoves: !settings.confirmMoves,
      },
    });
  };

  const toggleUndo = () => {
    dispatch({
      type: 'settings',
      payload: {
        ...settings,
        allowUndo: !settings.allowUndo,
      },
    });
  };

  const setDifficulty = (value) => {
    dispatch({
      type: 'settings',
      payload: {
        ...settings,
        difficultyLevel: value,
      },
    });
  };

  const setTotals = (value) => {
    const showTotals = !value ? 'off' : 'remainders';
    dispatch({
      type: 'settings',
      payload: {
        ...settings,
        showTotals,
      },
    });
  };

  const setBase = (value) => {
    const limit = parseInt(value, 10);
    const options = [];
    for (let i = 1; i <= limit; i += 1) {
      options.push(i);
    }
    dispatch({
      type: 'dice',
      payload: {
        ...dice,
        base: limit,
        options,
        blocker: limit + 1,
      },
    });
  };

  const prepLevel = (level) => {
    let text = '';
    switch (level) {
      case 'easy':
        text = 'Easy';
        break;
      case 'normal':
        text = 'Normal';
        break;
      case 'hard':
        text = 'Hard';
        break;
      default:
        break;
    }
    return ` ${text} `;
  };

  // @todo Make this pretty on iOS.
  // @todo Refactor to use FlatList as a table.
  return (
    <Modal
      onRequestClose={() => { return setModalVisible(null); }}
      visible={modalVisible}
      transparent
      testID="settingsModal"
    >
      <View style={BaseCss.modalSettingsContainer}>
        <Surface style={BaseCss.settingsModal} elevation={5}>
          <Text style={BaseCss.settingsHeader} testID="settingsHeader">
            GAME SETTINGS
          </Text>
          {/*
          <View style={BaseCss.settingsBody}>
            <View style={BaseCss.settingsFlexLeft}>
              <Text style={BaseCss.settingsText} testID="settingsHeader">
                Confirm Moves
              </Text>
            </View>
            <View style={BaseCss.settingsFlexRight}>
              <Switch
                value={settings.confirmMoves}
                onValueChange={toggleConfirm}
              />
            </View>
          </View>
          <View style={BaseCss.settingsBody}>
            <View style={BaseCss.settingsFlexLeft}>
              <Text style={BaseCss.settingsText} testID="settingsHeader">
                Allow Undo
              </Text>
            </View>
            <View style={BaseCss.settingsFlexRight}>
              <Switch
                value={settings.allowUndo}
                onValueChange={toggleUndo}
              />
            </View>
          </View>
          */}
          <View style={BaseCss.settingsBody}>
            <View style={BaseCss.settingsFlexLeft}>
              <Text style={BaseCss.settingsLabel} testID="settingsHeader">
                Show Hints
              </Text>
            </View>
            <View style={BaseCss.settingsFlexRight}>
              <Switch
                value={(settings.showTotals !== 'off')}
                onValueChange={setTotals}
              />
            </View>
          </View>
          <View style={BaseCss.settingsBody}>
            <View style={BaseCss.settingsFlexLeft}>
              <Text style={BaseCss.settingsLabel} testID="settingsHeader">
                Difficulty Level
              </Text>
            </View>
            {!settings.inProgress && (
              <RadioButton.Group
                onValueChange={(newValue) => {
                  setDifficulty(newValue);
                }}
                value={settings.difficultyLevel}
              >
                <RadioButton.Item label="Easy" value="easy" />
                <RadioButton.Item label="Normal" value="normal" />
                <RadioButton.Item label="Hard" value="hard" />
              </RadioButton.Group>
            )}
            {settings.inProgress && (
              <View style={BaseCss.settingsFlexRight}>
                <Text>
                  {prepLevel(settings.difficultyLevel)}
                </Text>
              </View>
            )}
          </View>
          <View style={BaseCss.settingsBody}>
            <View style={BaseCss.settingsFlexLeft}>
              <Text style={BaseCss.settingsLabel} testID="settingsHeader">
                Dice and Target Value
              </Text>
            </View>
            {!settings.inProgress && (
              <RadioButton.Group
                onValueChange={(newValue) => {
                  setBase(newValue);
                }}
                value={dice.base.toString()}
              >
                <RadioButton.Item label="Sixes" value="6" />
                <RadioButton.Item label="Eights" value="8" />
                <RadioButton.Item label="Tens" value="10" />
              </RadioButton.Group>
            )}
            {settings.inProgress && (
              <View style={BaseCss.settingsFlexRight}>
                <Text>
                  {` ${dice.base} `}
                </Text>
              </View>
            )}
          </View>
          <View style={BaseCss.settingsCloseButton}>
            <Pressable
              onPress={() => {
                setModalVisible(null);
              }}
              testId="settingsCloseButton"
              title="Close"
              style={BaseCss.button}
            >
              <Text style={BaseCss.buttonText}>Close</Text>
            </Pressable>
          </View>
        </Surface>
      </View>
    </Modal>
  );
};

Settings.propTypes = {
  modalVisible: PropTypes.bool,
  setModalVisible: PropTypes.func,
};
Settings.defaultProps = {
  modalVisible: false,
  setModalVisible: {},
};

export default Settings;
