/**
 * Indicates whether the game is over based on board being full.
 *
 * @param cells
 *   The cells object
 */
const isBoardFull = (cells) => {
  let isFull = true;
  // By default, the spaces are set to null. If no values are null, then
  // the game is over.
  Object.values(cells).every((cell) => {
    if (cell.type === 'board' && cell.value === null) {
      isFull = false;
    }
    return isFull;
  });

  return isFull;
};

export default isBoardFull;
