import React from 'react';
import {
  DefaultTheme,
  Provider as PaperProvider,
  Surface,
} from 'react-native-paper';
import { StatusBar } from 'expo-status-bar';
import Heading from './Layout/Heading/Heading';
import GameBoard from './Layout/GameBoard/GameBoard';
import GameState from './Context/GameContext/GameState';
import BaseCss from './Styles/BaseCss';

/**
 * Theming with Material Design 3.
 * https://github.com/callstack/react-native-paper/blob/main/src/styles/themes/v3/LightTheme.tsx
 */
const theme = {
  ...DefaultTheme,
  colors: {
    ...DefaultTheme.colors,
    primary: 'tomato',
    secondary: 'yellow',
    background: 'green',
  },
};

/**
 * Base entry point to the app.
 * @todo Add the CurrentGame context.
 *
 * @return {JSX.Element}
 */
const Index = () => {
  return (
    <GameState>
      <PaperProvider theme={theme}>
        <Surface style={BaseCss.surface} elevation={4} testID="surface">
          <Heading />
          <StatusBar />
          <GameBoard />
        </Surface>
      </PaperProvider>
    </GameState>
  );
};

export default Index;
