import React, { useContext } from 'react';
import {
  Button, Modal, Pressable, Text, View,
} from 'react-native';
import { Surface } from 'react-native-paper';
import PropTypes from 'prop-types';
import GameContext from '../../Context/GameContext/GameContext';
import BaseCss from '../../Styles/BaseCss';

const PlaceDie = ({ id, modalVisible, setModalVisible, setActive }) => {
  const {
    cells, status, dice, dispatch,
  } = useContext(GameContext);

  // List available blocker dice.
  const blockerOptions = [];
  /* eslint-disable no-plusplus */
  for (let i = 1; i <= status.blockersLeft; i++) {
    blockerOptions.push(i);
  }
  // @todo Move to defaultState.
  const blocker = 'X';

  /**
   * Callback to register die placement in state.
   * @param integer value
   */
  const placeDie = (value) => {
    let remaining = status.blockersLeft;
    if (value === dice.blocker) {
      remaining = status.blockersLeft - 1;
    }
    dispatch({
      type: 'status',
      payload: {
        ...status,
        lastMove: id,
        spacesUsed: status.spacesUsed + 1,
        blockersLeft: remaining,
      },
    });
    dispatch({
      type: 'cells',
      payload: {
        ...cells,
        [id]: {
          type: 'board',
          value: parseInt(value, 10),
        },
      },
    });
    setModalVisible(false);
  };

  return (
    <Modal
      onRequestClose={() => { return setModalVisible(false); }}
      visible={modalVisible}
      transparent
      testID="placeDieModal"
    >
      <View style={BaseCss.placementModal}>
        <Surface style={BaseCss.modalPlacement} elevation={5}>
          <View style={BaseCss.modalDice}>
            <View style={BaseCss.inlineWrapper}>
              {dice.options.map((value) => {
                return (
                  <View key={`die-${value}`}>
                    <Pressable
                      testID={`select-${id}`}
                      onPress={() => {
                        setActive(null);
                        placeDie(value);
                      }}
                      style={BaseCss.dieButton}
                    >
                      <Text testID={`placeDie-${value}`} style={BaseCss.dieButtonText}>{value}</Text>
                    </Pressable>
                  </View>
                );
              })}
            </View>
            <View style={BaseCss.inlineWrapper}>
              {blockerOptions.map((value) => {
                return (
                  <View key={`blocker-${value}`}>
                    <Pressable
                      testID={`select-blocker-${id}`}
                      onPress={() => {
                        setActive(null);
                        placeDie(dice.blocker);
                      }}
                      style={BaseCss.blockerButton}
                    >
                      <Text style={BaseCss.blockerButtonText}>{blocker}</Text>
                    </Pressable>
                  </View>
                );
              })}
            </View>
          </View>
          <View style={BaseCss.inlineWrapper}>
            <Pressable
              onPress={() => {
                setActive(null);
                setModalVisible(false);
              }}
              testId="cancelButton"
              key="button"
              style={BaseCss.darkButton}
            >
              <Text style={BaseCss.buttonText}>CANCEL</Text>
            </Pressable>
          </View>
        </Surface>
      </View>
    </Modal>
  );
};

PlaceDie.propTypes = {
  id: PropTypes.number,
  modalVisible: PropTypes.bool,
  setModalVisible: PropTypes.func,
  setActive: PropTypes.func,
};

PlaceDie.defaultProps = {
  id: null,
  modalVisible: true,
  setModalVisible: {},
  setActive: {},
};

export default PlaceDie;
