/**
 * Build sample data for the game.
 *
 * @param object cells
 *   The cells of the board.
 * @param object dice
 *   The dice object.
 */
const testGameboard = (cells, dice) => {
  const gameBoard = {};

  // Only allow 6 blocker dice.
  let blockers = 0;

  Object.values(cells).forEach((cell, index) => {
    if (cell.type === 'board') {
      let value = Math.floor(Math.random() * dice.blocker) + 1;
      if (value > dice.base) {
        blockers += 1;
        if (blockers > 6) {
          value -= (Math.floor(Math.random() * dice.base) + 1);
        }
      }
      const obj = {
        type: cell.type,
        value,
      };
      gameBoard[index] = obj;
    } else {
      gameBoard[index] = cell;
    }
  });

  return gameBoard;
};

export default testGameboard;
