/**
 * Calculates a player's final score.
 *
 * @param object cells
 *   The cells object, #0-99.
 * @param object dice
 *   The dice object.
 */

const calculateScore = (cells, dice) => {
  const score = {
    total: 0,
    items: [],
  };

  const targets = {};
  const sums = {};
  // Get the current targets.
  Object.values(cells).forEach((cell, id) => {
    if (cell.type === 'target') {
      targets[id] = cell.value;
    } else if (cell.type === 'sum' && cell.value > -1) {
      sums[id] = cell.value;
    }
  });

  // The corner squares score extra.
  // const bonusScores = [0, 9, 90, 99];

  Object.keys(sums).forEach((i) => {
    const index = parseInt(i, 10);
    const value = sums[index];
    const total = value % dice.base;
    let targetKey = index + 1;
    if (index === 0) { // Top left
      targetKey = 11;
    } else if (index === 9) { // Top right
      targetKey = 18;
    } else if (index < 10) { // Top row
      targetKey = index + 10;
    } else if (index % 10 === 0) { // Left side
      targetKey = index + 1;
    } else if (index % 10 === 9) { // Right side
      targetKey = index - 1;
    } else if (index === 90) { // Lower left
      targetKey = 81;
    } else if (index === 99) { // Lower right
      targetKey = 88;
    } else if (index > 90) { // Bottom row
      targetKey = index - 10;
    }

    if (targets[targetKey] === total) {
      score.total += 1;
      score.items.push(targetKey);
    }
  });

  return score;
};

export default calculateScore;
