import targetPairs from '../constants/targetPairs';
import uniqueValues from './uniqueValues';

/**
 * Build the initial gameboard state.
 *
 * @param bool difficultyLevel
 *   Indicates difficulty level.
 * @param object cells
 *   The cells object, #0-99.
 * @param object dice
 *   The dice object.
 */
const buildBoard = (difficultyLevel, cells, dice) => {
  const targets = {};

  // Loop through and set the targets on the cells.
  Object.values(cells).forEach((cell, index) => {
    if (cell.type === 'target') {
      const value = Math.floor(Math.random() * dice.base);
      targets[index] = {
        type: 'target',
        value,
      };
    } else {
      targets[index] = {
        type: cell.type,
        value: cell.value,
      };
    }
  });

  // Parallel targets cannot match.
  targetPairs.forEach((pair) => {
    const [one, two] = pair;
    if (targets[one].value === targets[two].value) {
      // Generate and select from a random array that does not
      // include the current value.
      const options = [];
      for (let i = 1; i < dice.blocker; i += 1) {
        if (i !== targets[one].value) {
          options.push(i);
        }
      }
      const random = Math.floor(Math.random() * options.length);
      targets[two].value = options[random];
    }
  });

  // Diagonals must match.
  // eslint-disable-next-line prefer-destructuring
  targets[88].value = targets[11].value;
  // eslint-disable-next-line prefer-destructuring
  targets[81].value = targets[18].value;

  // In harder difficultyLevel, 4 spaces are filled, 44, 45, 54, 55.
  if (difficultyLevel !== 'easy') {
    let preset = [44, 45, 54, 55];
    if (difficultyLevel === 'hard') {
      const presets = [];
      // Generate more than we need to prevent duplicates.
      for (let i = 0; i < 100; i += 1) {
        // Numbers between 22 and 77.
        const a = Math.ceil(Math.random() * 6) + 1;
        const b = (Math.ceil(Math.random() * 6) + 1) * 10;
        presets.push(a + b);
      }

      // Filter the list down to 4 items.
      preset = presets.filter(uniqueValues).slice(0, 4);
    }
    preset.forEach((value) => {
      targets[value].value = (Math.floor(Math.random() * dice.base) + 1);
    });
  }

  return targets;
};

export default buildBoard;
