import React, { useEffect, useMemo, useReducer } from 'react';
import PropTypes from 'prop-types';
import GameContext from './GameContext';
import defaultState from '../../_shared/constants/defaultState';
import GameContextReducer from './GameContextReducer';
import buildBoard from '../../_shared/utilities/buildBoard';
import getSums from '../../_shared/utilities/getSums';
import testGameboard from '../../_shared/utilities/testGameboard';
import isBoardFull from '../../_shared/utilities/isBoardFull';
import calculateScore from '../../_shared/utilities/calculateScore';

/**
 * The game state handler, which loads context to the app.
 *
 * @param children
 *   JSX.Element children. See Main.jsx.
 * @return {JSX.Element}
 *   The application wrappeed in the state handler.
 */
const GameState = ({ children }) => {
  const [state, dispatch] = useReducer(GameContextReducer, defaultState);

  // Build the board on the trigger.
  useEffect(() => {
    if (state.settings.inProgress && state.settings.boardSet && !isBoardFull(state.cells)) {
      const data = buildBoard(state.settings.difficultyLevel, state.cells, state.dice);
      dispatch({
        type: 'cells',
        payload: data,
      });
      // In difficultyLevel, the buildBoard method added 4 dice to the board.
      if (state.settings.difficultyLevel !== 'off') {
        dispatch({
          type: 'status',
          payload: {
            ...state.status,
            spacesUsed: 4,
          },
        });
      }
      if (state.settings.testMode) {
        dispatch({
          type: 'cells',
          payload: testGameboard(data, state.dice),
        });
        // Now trigger the calculations of sums.
        dispatch({
          type: 'status',
          payload: {
            lastMove: 77,
            spacesUsed: 36,
          },
        });
      }
    }
  }, [state.settings.inProgress]);

  // Update sums based on gameBoard changes and trigger game-end state.
  useEffect(() => {
    // Keep score.
    if (state.settings.inProgress && state.settings.boardSet) {
      const sums = getSums(state.cells, state.dice);
      dispatch({
        type: 'cells',
        payload: sums,
      });
      if (state.status.spacesUsed === 36) {
        dispatch({
          type: 'settings',
          payload: { ...state.settings, scoring: true },
        });
        dispatch({
          type: 'scoring',
          payload: calculateScore(sums, state.dice),
        });
      }
    }
  }, [state.status.spacesUsed]);

  // Set a memoized value for our context, else we hit render loops.
  const value = useMemo(() => {
    return {
      settings: state.settings,
      cells: state.cells,
      score: state.score,
      status: state.status,
      dice: state.dice,
      dispatch,
    };
  }, [state]);

  return (
    <GameContext.Provider value={value}>
      {children}
    </GameContext.Provider>
  );
};

GameState.propTypes = {
  children: PropTypes.node.isRequired,
};

export default GameState;
