import React, { useContext, useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { Modal, Pressable, Text, View } from 'react-native';
import { Surface } from 'react-native-paper';
import BaseCss from '../../Styles/BaseCss';
import GameContext from '../../Context/GameContext/GameContext';
import sleep from '../../_shared/utilities/sleep';
import defaultState from '../../_shared/constants/defaultState';

// Scoring modal.
const Scoring = ({ modalVisible, setModalVisible }) => {
  const { dice, score, settings, dispatch } = useContext(GameContext);
  const [finalScore, setFinalScore] = useState(null);

  useEffect(() => {
    sleep(2000).then(() => {
      setFinalScore(score.total);
    });
  }, []);

  const gameReset = () => {
    // Do a hard reset of all values.
    dispatch({
      type: 'reset',
      payload: {
        ...defaultState,
        settings,
        dice,
      },
    });
  };

  const getStyle = () => {
    let color = '#090';
    let borderWidth = 3;
    let borderColor = '#090';
    let backgroundColor = '#fff';
    if (finalScore <= 10) {
      color = '#930';
      borderColor = '#930';
      backgroundColor = '#fed';
    } else if (finalScore <= 20) {
      color = '#990';
      borderColor = '#990';
      backgroundColor = '#ffe';
    } else if (finalScore < 28) {
      color = '#090';
      borderColor = '#090';
      backgroundColor = '#ffe';
    }
    if (finalScore === 28) {
      color = '#0a0';
      borderColor = '#0a0';
      backgroundColor = '#fff';
      borderWidth = 5;
    }
    return {
      ...BaseCss.finalScore,
      color,
      backgroundColor,
      borderColor,
      borderWidth,
    };
  };

  const scoreLabel = () => {
    let value = '';
    if (finalScore <= 10) {
      value = 'An average score';
    } else if (finalScore <= 20) {
      value = 'A nice score';
    } else if (finalScore < 28) {
      value = 'An excellent score!';
    }
    if (finalScore === 28) {
      value = 'A PERFECT SCORE!!!';
    }
    return value;
  };

  return (
    <Modal
      onRequestClose={() => { return setModalVisible(false); }}
      visible={modalVisible}
      transparent
      testID="scoringModal"
    >
      <View style={BaseCss.scoringModal}>
        <Surface style={BaseCss.modalScoring} elevation={5}>
          {finalScore === null && (
            <Text style={BaseCss.scoringText}>Scoring...</Text>
          )}

          {finalScore !== null && (
            <>
              <Text style={BaseCss.scoringText}>Your Score</Text>
              <Text style={getStyle()}>{finalScore}</Text>
              <Text style={BaseCss.scoringText}>{scoreLabel()}</Text>
              <View>
                <Pressable
                  onPress={() => {
                    gameReset();
                    setModalVisible(null);
                  }}
                  style={BaseCss.button}
                  testId="resetButton"
                >
                  <Text style={BaseCss.buttonText}>New Game</Text>
                </Pressable>
                <Pressable
                  onPress={() => {
                    setModalVisible(null);
                  }}
                  style={BaseCss.darkButton}
                  testId="continueButton"
                >
                  <Text style={BaseCss.buttonText}>View Board</Text>
                </Pressable>
              </View>
            </>
          )}
        </Surface>
      </View>
    </Modal>
  );
};

Scoring.propTypes = {
  modalVisible: PropTypes.bool,
  setModalVisible: PropTypes.func,
};
Scoring.defaultProps = {
  modalVisible: false,
  setModalVisible: {},
};

export default Scoring;