import React, { useContext, useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { Image, Modal, Pressable, Text, View } from 'react-native';
import { Surface } from 'react-native-paper';
import BaseCss from '../../Styles/BaseCss';

// Help modal.
const Help = ({ modalVisible, setModalVisible }) => {
  const [page, setPage] = useState(0);

  const advance = () => {
    setPage(page + 1);
  };

  const back = () => {
    setPage(page - 1);
  };

  const pageTitle = () => {
    switch (page) {
      case 1:
        return 'EXAMPLE';
      case 0:
      default:
        return 'HOW TO PLAY';
    }
  };

  return (
    <Modal
      onRequestClose={() => { return setModalVisible(null); }}
      visible={modalVisible}
      transparent
      testID="helpModal"
    >
      <View style={BaseCss.modalSettingsContainer}>
        <Surface style={BaseCss.settingsModal} elevation={5}>
          <Text style={BaseCss.settingsHeader} testID="helpHeader">
            {pageTitle()}
          </Text>
          <Text style={BaseCss.plainText} testID="helpHeader">
            {page === 0 && (
              <>
                <p>
                  <em>Sixto </em>
                  is played on the 6 x 6 grid it is named for.
                </p>
                <ol>
                  <li style={BaseCss.list}>
                    Each turn you will place a numbered
                    <strong> die </strong>
                    on the grid.
                    Click an empty square to place a die. By default, these are a
                    <strong> 6-sixed die. </strong>
                    You can change to d8 or d10 in settings.
                  </li>
                  <li style={BaseCss.list}>
                    Numbers placed on the grid are added
                    in columns, rows, and the two full diagonals (each referred to as a
                    <strong> group</strong>
                    ).
                  </li>
                  <li style={BaseCss.list}>
                    Around the grid are
                    <strong> targets </strong>
                    that represent the desired value for the group.
                  </li>
                  <li style={BaseCss.list}>
                    At the end of the game, if the
                    <strong> remainder </strong>
                    of the total values in a
                    <strong> group </strong>
                    matches its
                    <strong> target </strong>
                    you score a point.
                  </li>
                  <li style={BaseCss.list}>
                    In addition to dice, you have six
                    <strong> blockers [X] </strong>
                    that you can use to divide a group and split it into different sums.
                  </li>
                  <li>28 is the maximum score.</li>
                </ol>
                <View style={BaseCss.helpButtons}>
                  <View style={BaseCss.inlineWrapper}>
                    <Pressable
                      onPress={() => {
                        advance();
                      }}
                      style={BaseCss.smallButton}
                      testId="nextButton"
                    >
                      <Text style={BaseCss.buttonText}>Next &raquo;</Text>
                    </Pressable>
                    <Pressable
                      onPress={() => {
                        setModalVisible(null);
                      }}
                      style={BaseCss.smallDarkButton}
                      testId="closeButton"
                    >
                      <Text style={BaseCss.buttonText}>Close</Text>
                    </Pressable>
                  </View>
                </View>
              </>
            )}
            {page === 1 && (
              <>
                <View style={BaseCss.helpButtons}>
                  <Image
                    source={require('./example.png')}
                    style={BaseCss.helpImage}
                  />
                </View>
                <p>
                  The inner ring shows the
                  <strong> targets </strong> for each group.
                  The outer ring shows the
                  <strong> remainder </strong>
                  value of the group, if any.
                </p>
                <View style={BaseCss.helpButtons}>
                  <View style={BaseCss.inlineWrapper}>
                    <Pressable
                      onPress={() => {
                        back();
                      }}
                      style={BaseCss.smallBlueButton}
                      testId="nextButton"
                    >
                      <Text style={BaseCss.buttonText}>&laquo; Back</Text>
                    </Pressable>
                    <Pressable
                      onPress={() => {
                        setModalVisible(null);
                      }}
                      style={BaseCss.smallDarkButton}
                      testId="closeButton"
                    >
                      <Text style={BaseCss.buttonText}>Close</Text>
                    </Pressable>
                  </View>
                </View>
              </>
            )}
          </Text>
        </Surface>
      </View>
    </Modal>
  );
};

Help.propTypes = {
  modalVisible: PropTypes.bool,
  setModalVisible: PropTypes.func,
};
Help.defaultProps = {
  modalVisible: false,
  setModalVisible: {},
};

export default Help;
