/**
 * Given the cells, calculates the sums of the board.
 *
 * @param object cells
 *   The cells object, #0-99.
 * @param object dice
 *   The dice object.
 */
const getSums = (cells, dice) => {
  const sums = {};
  const targets = {};
  const gameBoard = {};

  // Get the current board.
  Object.values(cells).forEach((cell, id) => {
    if (cell.type === 'board') {
      gameBoard[id] = cell;
    }
  });

  // Sums are helpers, which show the current value of a set.
  // 0-9, 10, 19, (etc.) 90-99 and marked as such.
  // The board is 22-27, 33-37, etc.
  const assignments = [
    { id: 0, board: [22, 33, 44, 55, 66, 77] },
    { id: 1, board: [] }, // Empty
    { id: 8, board: [] }, // Empty
    { id: 2, board: [22, 32, 42, 52, 62, 72] },
    { id: 3, board: [23, 33, 43, 53, 63, 73] },
    { id: 4, board: [24, 34, 44, 54, 64, 74] },
    { id: 5, board: [25, 35, 45, 55, 65, 75] },
    { id: 6, board: [26, 36, 46, 56, 66, 76] },
    { id: 7, board: [27, 37, 47, 57, 67, 77] },
    { id: 9, board: [27, 36, 45, 54, 63, 72] },
    { id: 10, board: [] }, // Empty
    { id: 19, board: [] }, // Empty
    { id: 20, board: [22, 23, 24, 25, 26, 27] },
    { id: 30, board: [32, 33, 34, 35, 36, 37] },
    { id: 40, board: [42, 43, 44, 45, 46, 47] },
    { id: 50, board: [52, 53, 54, 55, 56, 57] },
    { id: 60, board: [62, 63, 64, 65, 66, 67] },
    { id: 70, board: [72, 73, 74, 75, 76, 77] },
    { id: 80, board: [] }, // Empty
    { id: 89, board: [] }, // Empty
    { id: 91, board: [] }, // Empty
    { id: 98, board: [] }, // Empty
  ];
  const matchRows = [29, 39, 49, 59, 69, 79];
  matchRows.forEach((item) => {
    assignments.forEach((row) => {
      if (row.id === (item - 9)) {
        const add = { id: item, board: [...row.board].reverse() };
        assignments.push(add);
      }
    });
  });
  const matchCols = [92, 93, 94, 95, 96, 97];
  matchCols.forEach((item) => {
    assignments.forEach((row) => {
      if (row.id === (item - 90)) {
        const add = { id: item, board: [...row.board].reverse() };
        assignments.push(add);
      }
    });
  });
  // Diagonals are special.
  const matchDiagonal = [90, 99];
  matchDiagonal.forEach((item) => {
    assignments.forEach((row) => {
      if (item === 90 && row.id === 9) {
        const add = { id: item, board: [...row.board].reverse() };
        assignments.push(add);
      } else if (item === 99 && row.id === 0) {
        const add = { id: item, board: [...row.board].reverse() };
        assignments.push(add);
      }
    });
  });

  // Get the sums for active rows, cols, etc.
  assignments.forEach((item) => {
    // The value to print.
    let sum = 0;
    // Indicates that dice (including blockers) have been placed.
    let allSums = 0;
    if (item.board.length > 0) {
      item.board.every((square) => {
        allSums += gameBoard[square].value;
        if (gameBoard[square].value === dice.blocker) {
          return false;
        }
        sum += gameBoard[square].value;
        return true;
      });
      sums[item.id] = allSums > 0 ? sum : -1;
    }
  });

  // Loop through and set the targets on the cells.
  Object.values(cells).forEach((cell, index) => {
    if (cell.type === 'sum') {
      targets[index] = {
        type: 'sum',
        value: sums[index],
      };
    } else {
      targets[index] = {
        type: cell.type,
        value: cell.value,
      };
    }
  });

  return targets;
};

export default getSums;
