import { Platform, StyleSheet } from 'react-native';
import IosCss from './IosCss';
import WebCss from './WebCss';


/**
 * Default stylesheet for the application.
 *
 * @return object
 *   A stylesheet object for render.
 */
const BaseCss = Platform.select({
  ios: IosCss,
  default: WebCss,
});

export default BaseCss;
