import React, { useContext, useEffect, useState } from 'react';
import {
  SafeAreaView, StyleSheet, View, FlatList,
} from 'react-native';
import DiceDispatcher from '../../Dice/DiceDispatcher/DiceDispatcher';
import GameContext from '../../Context/GameContext/GameContext';
import BaseCss from "../../Styles/BaseCss";

/**
 * Builds the grid layout.
 *
 * @return {JSX.Element}
 */
const GameBoard = () => {
  const { cells, settings } = useContext(GameContext);
  const [dataSource, setDataSource] = useState([]);
  // The setActiveCell state function is passed as a prop to children.
  const [activeCell, setActiveCell] = useState(null);

  // Set the grid.
  useEffect(() => {
    // eslint-disable-next-line
    const items = Array.apply(null, Array(100)).map((v, i) => {
      return {
        id: i,
      };
    });
    setDataSource(items);
  }, []);

  /**
   * Determines the cell border style.
   * @param type
   * @returns object
   */
  const getStyle = (id, type) => {
    let borderWidth = 1;
    let backgroundColor = '#fff';
    let borderColor = '#000';
    if ((type === 'sum' || type === 'empty') && settings.showTotals === 'off') {
      borderColor = '#ddd';
    }
    if (id === activeCell) {
      backgroundColor = '#fc6';
      borderColor = '#fff';
    }
    return {
      ...BaseCss.cells,
      borderWidth,
      backgroundColor,
      borderColor,
    };
  };

  return (
    <SafeAreaView style={BaseCss.container}>
      <FlatList
        data={dataSource}
        renderItem={({ item }) => {
          return (
            <View style={getStyle(item.id, cells[item.id].type)}>
              <DiceDispatcher
                id={item.id}
                value={cells[item.id].value}
                type={cells[item.id].type}
                setActive={setActiveCell}
              />
            </View>
          );
        }}
        numColumns={10}
        keyExtractor={(item, index) => { return index; }}
      />
    </SafeAreaView>
  );
};

export default GameBoard;
