import React, { useContext, useState } from 'react';
import PropTypes from 'prop-types';
import { View } from 'react-native';
import { Button, Text } from 'react-native-paper';
import GameContext from '../../Context/GameContext/GameContext';
import PlaceDie from '../../Modals/PlaceDie/PlaceDie';
import BaseCss from '../../Styles/BaseCss';

/**
 * Handles the display of dice and their values.
 *
 * @param id
 *   The die grid location id.
 * @param value
 *   The die value, where 0 indicates blank.
 * @param color
 *   The color of the die.
 * @param backgroundColor
 *   The color for the die background.
 *
 * @returns {JSX.Element}
 *   The render element.
 */
const DiceDispatcher = ({ id, value, type, setActive }) => {
  const { dice, settings, score } = useContext(GameContext);
  const [modalVisible, setModalVisible] = useState(false);
  // Text for the dice.
  const blockerValue = dice?.blocker || 7;

  let diceValue = ((type === 'board' && value === blockerValue) ? 'X' : value);
  // Sums are "off", "totals" or "remainders".
  if (type === 'sum') {
    if (value === -1 || settings.showTotals === 'off') {
      diceValue = '';
    } else if (settings.showTotals === 'remainders') {
      diceValue %= dice.base;
    }
  }

  let color = '#900';
  let backgroundColor = '#ddd';
  if (type === 'board') {
    color = '#000';
    backgroundColor = '#cec';
  } else if (type === 'target') {
    color = '#383';
    backgroundColor = '#eee';
    if (settings.scoring) {
      if (score.items.includes(id)) {
        backgroundColor = '#090';
        color = '#fff';
      }
    }
  }
  if (type === 'board' && value === blockerValue) {
    color = '#900';
    backgroundColor = '#ca8';
  }
  if (type === 'sum' && settings.showTotals !== 'off') {
    backgroundColor = '#ccc';
    if (value >= 0) {
      backgroundColor = '#acd';
    }
  }
  if (type === 'empty' && settings.showTotals !== 'off') {
    backgroundColor = '#666';
  }

  // Merge default styles with the caller.
  const style = {
    backgroundColor,
    height: '100%',
    width: '100%',
    justifyContent: 'center',
  };

  const text = {
    ...BaseCss.die,
    color,
  };

  // Determines if a cell can be pressed.
  const isSelectable = settings.boardSet && settings.inProgress && value === null && type === 'board';

  // The parser wants a wrapper around the IF, and ESLint does not.
  /* eslint-disable react/jsx-no-useless-fragment, react/jsx-curly-newline */
  return (
    <>
      {isSelectable
        ? (
          <>
            {modalVisible
              ? (
                <PlaceDie
                  id={id}
                  modalVisible={modalVisible}
                  setModalVisible={setModalVisible}
                  setActive={setActive}
                />
              )
              : (
                <Button
                  onPress={() => {
                    setActive(id);
                    setModalVisible(true);
                  }}
                  testId={`place-${id}`}
                  title="X"
                />
              )
          }
          </>
        )
        : (
          <View style={style}>
            <Text style={text} testID={`die${id}-${value}`}>{diceValue}</Text>
          </View>
        )
      }
    </>
  );
};

DiceDispatcher.propTypes = {
  id: PropTypes.number,
  value: PropTypes.number,
  type: PropTypes.string,
  setActive: PropTypes.func,
};

DiceDispatcher.defaultProps = {
  id: null,
  value: 0,
  type: '',
  setActive: {},
};

export default DiceDispatcher;
