/**
 * The default state values of the application.
 */
const defaultState = {
  settings: {
    inProgress: false,
    boardSet: false,
    difficultyLevel: 'normal', // "easy", or "hard" or "normal"
    testMode: false,
    showTotals: 'remainders', // "off", or "totals" or "remainders"
    scoring: false,
    confirmMoves: true,
    allowUndo: true,
  },
  dice: {
    blocker: null,
    blockersAllowed: 6,
    options: [],
    base: 6,
  },
  cells: {},
  score: {
    total: 0,
    items: [],
  },
  // Keep track of what the player has done.
  status: {
    blockersLeft: 6,
    spacesUsed: 0,
    lastMove: '',
  },
};

// Set the default state values.
for (let i = 0; i < 100; i += 1) {
  if (i < 10 || i % 10 === 0 || i % 10 === 9 || i > 90) {
    // Sums are helpers, which show the current value of a set.
    // 0-9, 10, 19, (etc.) 90-99.
    defaultState.cells[i] = {
      type: 'sum',
      value: -1,
    };
  } else if (i < 20 || i % 10 === 1 || i % 10 === 8 || i > 80) {
    // Targets ring the board, 11-18, 21, 28, (etc.) 81-88.
    defaultState.cells[i] = {
      type: 'target',
      value: null,
    };
  } else {
    // The board is 22-27, 33-37, etc.
    defaultState.cells[i] = {
      type: 'board',
      value: null,
    };
  }
  const empty = [1, 8, 10, 19, 80, 89, 91, 98];
  empty.forEach((id) => {
    defaultState.cells[id] = {
      type: 'empty',
      value: null,
    };
  });
}

// Set the dice values.
for (let i = 1; i <= defaultState.dice.base; i += 1) {
  defaultState.dice.options.push(i);
}
defaultState.dice.blocker = defaultState.dice.base + 1;

export default defaultState;
