/**
 * The reducer for application state.
 * @param state
 * @param type
 * @param payload
 * @constructor
 */
const GameContextReducer = (state, { type, payload }) => {
  let value = state;
  switch (type) {
    case 'settings':
      value = {
        ...state,
        settings: payload,
      };
      break;
    case 'dice':
      value = {
        ...state,
        dice: payload,
      };
      break;
    case 'cells':
      value = {
        ...state,
        cells: payload,
      };
      break;
    case 'scoring':
      value = {
        ...state,
        score: payload,
      };
      break;
    case 'status':
      value = {
        ...state,
        status: payload,
      };
      break;
    case 'reset':
      value = {
        ...payload,
        settings: {
          ...payload.settings,
          inProgress: false,
          boardSet: false,
          scoring: false,
        },
      };
      break;
    default:
      break;
  }

  // Return the new value.
  return value;
};

export default GameContextReducer;
