import React, { useContext, useEffect, useState } from 'react';
import { Pressable, Text, View } from 'react-native';
import { IconButton } from 'react-native-paper';
import BaseCss from '../../Styles/BaseCss';
import GameContext from '../../Context/GameContext/GameContext';
import Help from '../../Modals/Help/Help';
import QuitGame from '../../Modals/QuitGame/QuitGame';
import Scoring from '../../Modals/Scoring/Scoring';
import Settings from '../../Modals/Settings/Settings';

/**
 * The application header / branding.
 *
 * @return {JSX.Element}
 */
const Heading = () => {
  // Can be null, help, quit, scoring, or settings.
  const [modal, setModal] = useState('help');
  const [modalVisible, setModalVisible] = useState(true);
  const { settings, dispatch } = useContext(GameContext);

  useEffect(() => {
    if (settings.scoring) {
      setModal('scoring');
      setModalVisible(true);
    }
  }, [settings.scoring]);

  // Starts a new game.
  const startGame = (inProgress, boardSet) => {
    dispatch({
      type: 'settings',
      payload: {
        ...settings,
        inProgress: !inProgress,
        boardSet: !boardSet,
        scoring: false,
      },
    });
  };
  return (
    <>
      <View style={BaseCss.headerContainer}>
        <View style={BaseCss.settingsLeft}>
          <IconButton
            icon="help-circle-outline"
            iconColor="#069"
            size={28}
            onPress={() => {
              setModal('help');
              setModalVisible(true);
            }}
          />
        </View>
        <Text style={BaseCss.header} testID="header">
          S I X T O
        </Text>
        <View style={BaseCss.settings}>
          <IconButton
            icon="cog-outline"
            iconColor="#069"
            size={28}
            onPress={() => {
              setModal('settings');
              setModalVisible(true);
            }}
          />
        </View>
      </View>
      <View>
        {modalVisible && modal === 'quit' && (
          <QuitGame
            modalVisible={modalVisible}
            setModalVisible={setModalVisible}
          />
        )}
        {modalVisible && modal === 'help' && (
          <Help
            modalVisible={modalVisible}
            setModalVisible={setModalVisible}
          />
        )}
        {modalVisible && modal === 'settings' && (
          <Settings
            modalVisible={modalVisible}
            setModalVisible={setModalVisible}
          />
        )}
        {modalVisible && modal === 'scoring' && (
          <Scoring
            modalVisible={modalVisible}
            setModalVisible={setModalVisible}
          />
        )}
        {settings.inProgress === false
          && (
            <Pressable
              onPress={() => {
                startGame(settings.inProgress, settings.boardSet);
              }}
              style={BaseCss.button}
              testID="startButton"
            >
              <Text style={BaseCss.buttonText}>START NEW GAME</Text>
            </Pressable>
          )}
        {settings.inProgress === true
          && (
            <Pressable
              onPress={() => {
                setModal('quit');
                setModalVisible(true);
              }}
              style={BaseCss.darkButton}
              testID="quitButton"
            >
              <Text style={BaseCss.buttonText}>QUIT GAME</Text>
            </Pressable>
          )}
      </View>
    </>
  );
};

export default Heading;
