import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import {
  Modal, Pressable, Text, View,
} from 'react-native';
import { Surface } from 'react-native-paper';
import BaseCss from '../../Styles/BaseCss';
import defaultState from '../../_shared/constants/defaultState';
import GameContext from '../../Context/GameContext/GameContext';

// Allows players to confirm quit.
const QuitGame = ({ modalVisible, setModalVisible }) => {
  const { dice, settings, dispatch } = useContext(GameContext);
  const gameReset = () => {
    // Do a hard reset of all values.
    dispatch({
      type: 'reset',
      payload: {
        ...defaultState,
        settings,
        dice,
      },
    });
  };

  return (
    <Modal
      onRequestClose={() => { return setModalVisible(null); }}
      visible={modalVisible}
      transparent
      testID="quitModal"
    >
      <View style={BaseCss.modal}>
        <Surface style={BaseCss.modalContent} elevation={5}>
          <Text style={BaseCss.quitText} testID="quitDialog">
            QUIT GAME?
          </Text>
          <View style={BaseCss.divider} />
          <View style={BaseCss.inlineWrapper}>
            <Pressable
              onPress={() => {
                gameReset();
                setModalVisible(null);
              }}
              style={BaseCss.smallButton}
              testId="resetButton"
            >
              <Text style={BaseCss.buttonText}>YES</Text>
            </Pressable>
            <Pressable
              onPress={() => {
                setModalVisible(null);
              }}
              style={BaseCss.smallDarkButton}
              testId="continueButton"
            >
              <Text style={BaseCss.buttonText}>NO</Text>
            </Pressable>
          </View>
        </Surface>
      </View>
    </Modal>
  );
};

QuitGame.propTypes = {
  modalVisible: PropTypes.bool,
  setModalVisible: PropTypes.func,
};
QuitGame.defaultProps = {
  modalVisible: false,
  setModalVisible: {},
};

export default QuitGame;
